import "./contact.css";
import contact from "../../assets/contact-img.png";
export default function Contact() {
  return (
    <section id="contact" className="contact">
      <div className="container">
        <div className="image">
          <img src={contact} alt="image" />
        </div>
        <div className="info">
          <h3>Contact Now</h3>
          <div className="details">
            <div className="box">
              <p>Write Us</p>
              <div className="icons">
                <a href="#">
                  <i className="fa-brands fa-whatsapp" />
                </a>
                <a href="#">
                  <i className="fa-brands fa-facebook-messenger" />
                </a>
                <a href="#">
                  <i className="fa-brands fa-telegram" />
                </a>
              </div>
            </div>
            <div className="box">
              <p>Delivery</p>
              <span>+00-987-7654-321</span>
              <br />
              <span>+11-0123456</span>
            </div>
            <div className="box">
              <p>Location</p>
              <span>
                Lima-Sun City-Peru
                <br />
                Av.Moon #4321
              </span>
              <br />
              <a href="#">
                <i className="fa-solid fa-location-dot" /> View On Map
              </a>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}
