import "./about.css";
import about from "../../assets/about-img.png";
import sticker_cheese from "../../assets/sticker-cheese.svg";
export default function About() {
  return (
    <section id="about-us" className="about-us">
      <div className="container">
        <div className="image">
          <img src={about} alt="image" />
        </div>
        <div className="info">
          <h3>
            Learn More About <br />
            Our History
          </h3>
          <p>
            The secret to success is the beast ingredients to make a better
            pizza. Today we strive for perfection,just us we have been doing for
            15 years when we first open th pizzeria in Lima - Peru
          </p>
          <img className="sticker" src={sticker_cheese} alt="image" />
          <a href="#">The Beast Pizzas</a>
        </div>
      </div>
    </section>
  );
}
