import "./recipe.css";
import recipe_flour from "../../assets/recipe-flour.png";
import recipe_cheese from "../../assets/recipe-cheese.png";
import recipe_sauces from "../../assets/recipe-sauces.png";
import recipe_tomato from "../../assets/recipe-tomato.png";
import recipe_meat from "../../assets/recipe-meat.png";
import recipe from "../../assets/recipe-img.png";
export default function Recipe() {
  return (
    <section id="recipe" className="recipe">
      <h3>
        Fresh And <br />
        Natural Ingredients
      </h3>
      <div className="container">
        <div className="cards">
          <article className="box">
            <img src={recipe_flour} alt="image" />
            <div className="text">
              <h3>Flour</h3>
              <p>The best wheat from the field for the best flour.</p>
            </div>
          </article>
          <article className="box">
            <img src={recipe_cheese} alt="image" />
            <div className="text">
              <h3>Cheese</h3>
              <p>Indulge in cheese for a healthy future.</p>
            </div>
          </article>
          <article className="box">
            <img src={recipe_sauces} alt="image" />
            <div className="text">
              <h3>Sauces</h3>
              <p>Add a touch of salsa to your life and it will taste better.</p>
            </div>
          </article>
          <article className="box">
            <img src={recipe_tomato} alt="image" />
            <div className="text">
              <h3>Vegetables</h3>
              <p>Vegetables full of the essence of nature and organic.</p>
            </div>
          </article>
          <article className="box">
            <img src={recipe_meat} alt="image" />
            <div className="text">
              <h3>Meats</h3>
              <p>The best fresh meats for your health.</p>
            </div>
          </article>
        </div>
        <div className="image">
          <img src={recipe} alt="image" />
        </div>
      </div>
    </section>
  );
}
