import { useEffect } from "react";
import logo from "../../assets/logo-pizza.svg";
import "./header.css";
export default function Header() {
  useEffect(() => {
    let bars = document.querySelector(".bars");
    let xmark = document.querySelector(".xmark");
    let nav = document.querySelector(".nav");
    let links = document.querySelectorAll(".nav li");
    let landing = document.querySelector(".landing");

    const mediaQuery = window.matchMedia("(max-width: 767px)");

    if (mediaQuery.matches) {
      bars.addEventListener("click", () => {
        xmark.style.display = "flex";
        bars.style.display = "none";
        nav.style.display = "flex";
        landing.style.opacity = "0.2";
      });

      xmark.addEventListener("click", () => {
        bars.style.display = "flex";
        xmark.style.display = "none";
        nav.style.display = "none";
        landing.style.opacity = "1";
      });

      links.forEach((e) => {
        e.addEventListener("click", () => {
          nav.style.display = "none";
          bars.style.display = "flex";
          xmark.style.display = "none";
          landing.style.opacity = "1";
        });
      });
    }
    mediaQuery.addEventListener("change", (e) => {
      if (!e.matches) {
        bars.style.display = "none";
        xmark.style.display = "none";
        nav.style.display = "flex";
      }
    });

    /* --------------------------------------------------------- */
    /* --------------------------------------------------------- */
    /* --------------------------------------------------------- */

    const navItems = document.querySelectorAll(".main-nav li a");
    navItems.forEach((item) => {
      item.addEventListener("click", function () {
        navItems.forEach((nav) => nav.classList.remove("active"));
        this.classList.add("active");
      });
    });
  }, []);
  return (
    <header>
      <div className="container">
        <a href="#">
          <img src={logo} />
          <span>Pizza</span>
        </a>
        <div className="icons">
          <i className="fa-solid fa-xmark xmark" />
          <i className="fa-solid fa-bars bars" />
        </div>
        <ul className="main-nav nav">
          <li>
            <a className="active" href="#landing">
              Home
            </a>
          </li>
          <li>
            <a href="#about-us">About Us</a>
          </li>
          <li>
            <a href="#popular">Popular</a>
          </li>
          <li>
            <a href="#recipe">Recipe</a>
          </li>
          <li>
            <a href="#products">Products</a>
          </li>
          <li>
            <a href="#contact">Contact</a>
          </li>
        </ul>
      </div>
    </header>
  );
}
