import "./popular.css";
import { useEffect } from "react";
import popular_dish from "../../assets/popular-dish.png";
import popular_1 from "../../assets/popular-1.png";
import popular_2 from "../../assets/popular-2.png";
import popular_3 from "../../assets/popular-3.png";

export default function Popular() {
  useEffect(() => {
    const pizzaImages = document.querySelectorAll(".pizza img");
    const prevButton = document.getElementById("prev");
    const nextButton = document.getElementById("next");

    let currentPizzaIndex = 0;

    function updatePizzaImage() {
      pizzaImages.forEach((img, index) => {
        if (index === currentPizzaIndex) {
          img.classList.remove("hidden");
        } else {
          img.classList.add("hidden");
        }
      });
    }

    nextButton.addEventListener("click", () => {
      currentPizzaIndex = (currentPizzaIndex + 1) % pizzaImages.length;
      updatePizzaImage();
    });

    prevButton.addEventListener("click", () => {
      currentPizzaIndex =
        (currentPizzaIndex - 1 + pizzaImages.length) % pizzaImages.length;
      updatePizzaImage();
    });

    updatePizzaImage();

    setInterval(() => {
      currentPizzaIndex = (currentPizzaIndex + 1) % pizzaImages.length;
      updatePizzaImage();
    }, 2000);

    /* ---------------------------------------------------- */
    /* ---------------------------------------------------- */
    /* ---------------------------------------------------- */

    let startX = 0;
    pizzaImages[0].parentElement.addEventListener("touchstart", (e) => {
      startX = e.touches[0].clientX;
    });

    pizzaImages[0].parentElement.addEventListener("touchend", (e) => {
      let endX = e.changedTouches[0].clientX;

      if (startX - endX > 50) {
        currentPizzaIndex = (currentPizzaIndex + 1) % pizzaImages.length;
      } else if (endX - startX > 50) {
        currentPizzaIndex =
          (currentPizzaIndex - 1 + pizzaImages.length) % pizzaImages.length;
      }

      updatePizzaImage();
    });
  }, []);
  return (
    <section id="popular" className="popular">
      <div className="container">
        <div className="info">
          <h3>
            Discover <br />
            Popular Orders
          </h3>
          <p>
            Select the best prepared and delicious flavors. We have collected
            some popular recipes from around the world for you to choose your
            favorite.
          </p>
        </div>
        <div className="pizza-container">
          <div className="dish">
            <img className="dish-image" src={popular_dish} alt="image" />
          </div>
          <div className="pizza">
            <img id="pizza-image" src={popular_1} alt="image" />
            <img className="hidden" src={popular_2} alt="image" />
            <img className="hidden" src={popular_3} alt="image" />
          </div>
          <i className="fa-solid fa-arrow-left left" id="prev" />
          <i className="fa-solid fa-arrow-right right" id="next" />
        </div>
      </div>
    </section>
  );
}
