import "./landing.css";
import sticker_pizza from "../../assets/sticker-pizza.svg";
import sticker_leaf from "../../assets/sticker-leaf.svg";
import home_pizza from "../../assets/home-pizza.png";
import home_board from "../../assets/home-board.png";
import home_leaf_1 from "../../assets/home-leaf-1.png";
import home_leaf_2 from "../../assets/home-leaf-2.png";
import home_pepperoni from "../../assets/home-pepperoni.png";
import home_mushroom from "../../assets/home-mushroom.png";
import home_olive from "../../assets/home-olive.png";
import home_tomato from "../../assets/home-tomato.png";
export default function Landing() {
  return (
    <section id="landing" className="landing">
      <div className="container">
        <div className="info">
          <h1>Crispy And Delicious Pizza</h1>
          <p>
            Order the best Pizza to end your hunger and make your family
            <br />
            moments more memorable, place your order now
          </p>
          <a href="#">Order Pizza Now</a>
          <img className="sticker-one" src={sticker_pizza} alt="image" />
          <img className="sticker-two" src={sticker_leaf} alt="image" />
        </div>
        <div className="image">
          <img className="pizza-image" src={home_pizza} alt="image" />
          <img className="board-image" src={home_board} alt="image" />
          <img className="leaf-one-image" src={home_leaf_1} alt="image" />
          <img className="leaf-two-image" src={home_leaf_2} alt="image" />
          <img className="pepperoni-image" src={home_pepperoni} alt="image" />
          <img className="mushroom-image" src={home_mushroom} alt="image" />
          <img className="olive-image" src={home_olive} alt="image" />
          <img className="tomato-image" src={home_tomato} alt="image" />
        </div>
      </div>
    </section>
  );
}
