import "./products.css";
import product_1 from "../../assets/product-1.png";
import product_2 from "../../assets/product-2.png";
import product_3 from "../../assets/product-3.png";
import product_4 from "../../assets/product-4.png";
import product_5 from "../../assets/product-5.png";
export default function Products() {
  return (
    <section id="products" className="products">
      <div className="container">
        <h3>
          The Most <br />
          Devoured Pizzas
        </h3>
        <div className="cards">
          <article className="box">
            <img src={product_1} alt="image" />
            <p>
              Vegetarian <br />
              Pizza
            </p>
            <div className="info">
              <span>$15</span>
              <i className="fa-solid fa-store" />
            </div>
          </article>
          <article className="box">
            <img src={product_2} alt="image" />
            <p>
              Pepperoni <br />
              Pizza
            </p>
            <div className="info">
              <span>$10</span>
              <i className="fa-solid fa-store" />
            </div>
          </article>
          <article className="box">
            <img src={product_3} alt="image" />
            <p>
              Margherita <br />
              Pizza
            </p>
            <div className="info">
              <span>$12</span>
              <i className="fa-solid fa-store" />
            </div>
          </article>
          <article className="box">
            <img src={product_4} alt="image" />
            <p>
              Mushroom <br />
              Pizza
            </p>
            <div className="info">
              <span>$17</span>
              <i className="fa-solid fa-store" />
            </div>
          </article>
          <article className="box">
            <img src={product_5} alt="image" />
            <p>
              Italian
              <br />
              Pizza
            </p>
            <div className="info">
              <span>$17</span>
              <i className="fa-solid fa-store" />
            </div>
          </article>
        </div>
      </div>
    </section>
  );
}
